import uniStatusBar from '@/components/uni-ui/uni-status-bar/uni-status-bar.vue';
import otherLogin from '@/components/common/other-login.vue';
export default {
	components: {
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			status:false,
			username:"",
			password:"",
			phone:"",
			code:"",
			codeTime:0,
			loading:false
		}
	},
	onLoad() {
		
	},
	computed: {
		disabled() {
			if ((this.username === '' || this.password === '') && (this.phone === '' || this.code === '')) {
				return true
			}
			return false
		}
	},
	methods: {
		back(){
			uni.navigateBack({
				delta: 1
			});
		},
		// 初始化表单
		initForm(){
			this.username = ''
			this.password = ''
			this.phone = ''
			this.code = ''
		},
		// 切换登录方式
		changeStatus(){
			// 初始化表单
			this.initForm()
			this.status = !this.status
		},
		// 获取验证码
		getCode(){
			// 防止重复获取
			if (this.codeTime > 0) {
				return;
			}
			// 验证手机号
			if (!this.validate()) return;
			// 请求数据
			this.$H.post('/user/sendcode',{
				phone:this.phone
			},{
				native:true
			}).then(res=>{
				uni.showToast({
					title: res.data.msg,
					icon: 'none'
				});
				// 倒计时
				this.codeTime = 60
				let timer = setInterval(()=>{
					if (this.codeTime >= 1) {
						this.codeTime--
					} else {
						this.codeTime = 0
						clearInterval(timer)
					}
				},1000)
			})
		},
		// 表单验证
		validate(){
			//手机号正则
			var mPattern = /^1[34578]\d{9}$/; 
			if (!mPattern.test(this.phone)) {
				uni.showToast({
					title: '手机号格式不正确',
					icon: 'none'
				});
				return false
			}
			// ...更多验证
			return true
		},
		// 提交
		submit(){
			// 表单验证
			let url = ""
			let data = ""
			if(this.status){
				// 手机验证码登录
				if (!this.validate()) return;
				url = '/user/phonelogin'
				data = {
					phone:this.phone,
					code:this.code
				}
			} else {
				// 账号密码登录
				url = '/user/login'
				data = {
					username:this.username,
					password:this.password
				}
			}
			// 提交后端
			this.loading = true
			this.$H.post(url,data).then(res=>{
				this.loading = false
				console.log(res);
				// 修改vuex的state,持久化存储
				this.$store.commit('login',res)
				// 提示和跳转
				uni.navigateBack({
					delta: 1
				}); 
				uni.showToast({
					title: '登录成功',
					icon: 'none'
				});
			}).catch(err=>{
				// 登录失败
				this.loading = false
			})		
						
		}
	}
}
